.page {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
}

.page__content {
  position: relative;
  flex: 1 0 auto;
  min-height: 150px;
}

.page__content-wrap {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
}

.page__footer {
  flex: 0 0 auto;
}

.block {
  display: flex;
}

.header {
  height: 54px;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 10;
}

.header__inner {
  position: relative;
  height: 100%;
  background-color: #2471be;
  z-index: 10;
}

.header__wrap {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  max-width: 115px;
}

.header__logo img {
  display: block;
  width: 100%;
}

.header .icon-icons_close {
  display: none;
}

.header .icon-icons_menu {
  display: block;
}

.header.is-open-menu .icon-icons_close {
  display: block;
}

.header.is-open-menu .icon-icons_menu {
  display: none;
}

.main-menu {
  padding-top: 54px;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  overflow: auto;
  background-color: #fff;
  z-index: -1;
  display: none;
}

.header.is-open-menu .main-menu {
  display: block;
}

.main-menu__wrap {
  padding: 24px 0;
  display: flex;
  flex-wrap: wrap;
  margin: 0 -8px;
  height: 100%;
}

.main-menu__item {
  width: 50%;
  min-height: 90px;
  padding: 4px 8px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.main-menu__item-content {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
  border: 1px solid #dfdfdf;
}

.main-menu__icon {
  width: 24px;
  height: 24px;
  color: #a7a7aa;
}

.main-menu__icon .icon {
  display: block;
  font-size: 24px;
}

.main-menu__name {
  font-size: 13px;
  margin-top: 10px;
}

.main-menu__footer {
  width: 100%;
  height: 50px;
  background-color: #f1f1f5;
}

.main-menu__footer-icon .icon {
  display: block;
  font-size: 21px;
}

.main-menu__footer-text {
  margin-left: 10px;
}

.main-menu__desktop {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

